import React, { useRef, useEffect } from 'react';

import '../assets/scss/contact.scss';

import Layout from '../components/Layout';
import emailjs from '@emailjs/browser';
import gsap from 'gsap';



const Contact = (props) => {

	const form = useRef();

	const textRef = useRef();
	const formUpRef = useRef();

	useEffect(() => {
		let tl = gsap.timeline(); //
		tl.from(textRef.current, {
			y: 100,
			opacity: 0,
			duration: 1,
			ease: "ease.out",
		});

		tl.from(formUpRef.current, {
			y: 100,
			opacity: 0,
			duration: 1,
			ease: "ease.out",
		})

	}, []);


	//
  const sendEmail = (e) => {
    e.preventDefault();

		//    
		const name = form.current.querySelector('#contact_name');
		const email = form.current.querySelector('#contact_email');
		const message = form.current.querySelector('#contact_message');

		//  
		if (!name.value) {
			alert('  .');
			name.focus();
			return;
		} 

		if (!email.value) {
			alert('   ');
			email.focus();
			return;
		}

		if (!message.value) {
			alert('  ');
			message.focus();
			return;
		}

		// 
    emailjs.sendForm('service_7edqrss', 'template_v2umrrj', form.current, {
        publicKey: 'upGj3_ip3VdmDn5j9',
      })
      .then(
        () => {
					alert("  .");
					form.current.reset();
        },
        (error) => {
					console.log(error.text);
					alert("  .");
        },
      );
  };


  return (
		<Layout header>
			<div className='contact-wrap'>
				<div ref={textRef} className='contact-info'>
					<h2>Contact Me.</h2>
					<p>    !<br/>   .</p>
					<div className='box'>
						<p>: <a href='tel:+821031938441'>+82 10-3193-8441</a></p>
						<p>: <a href='mailto:pmineq.12@gmail.com'>pmineq.12@gmail.com</a></p>
					</div>
				</div>
				<div ref={formUpRef} className='contact-letter'>
					<form ref={form} onSubmit={sendEmail}>
						<div className='input-wrap'>
							<label htmlFor='contact_name'></label>
							<input type="text" id="contact_name" name="contact_name" />
						</div>
						<div className='input-wrap'>
							<label htmlFor='contact_email'>  </label>
							<input type="email" id="contact_email" name="contact_email" placeholder='email@mail.com' />
						</div>
						<div className='input-wrap'>
							<label htmlFor='contact_message'></label>
							<textarea id='contact_message' name="contact_message"/>
						</div>
						<input type="submit" value="" />
					</form>
				</div>
			</div>
		</Layout>
	);
};


export default Contact;