import React, { useEffect, useRef } from "react";
import $ from 'jquery';
import gsap from 'gsap';
import { ScrollTrigger } from "gsap/ScrollTrigger";

import Layout from '../../components/Layout';
import '../../assets/scss/portfolio.scss';
import Topimg from '../../assets/images/project/lsgpis.png';
import Lsgpis01 from '../../assets/images/project/lsgpis_sub01.jpg';
import Lsgpis02 from '../../assets/images/project/lsgpis_sub02.jpg';
import Lsgpis03 from '../../assets/images/project/lsgpis_sub03.png';
import Lsgpis04 from '../../assets/images/project/lsgpis_sub04.png';
import Lsgpis05 from '../../assets/images/project/lsgpis_sub05.png';
import Lsgpis06 from '../../assets/images/project/lsgpis_sub06.png';


const Lsgpis = () => {

	gsap.registerPlugin(ScrollTrigger);
	const projectRef = useRef();
	const topRef = useRef();

	const textRef = useRef();

	useEffect(() => {

		$('#header').addClass('white difference');

		gsap.to(topRef.current,{
			height: '70vh',
			duration: 3,
			onComplete: () => {
        //    ScrollTrigger 
        ScrollTrigger.refresh();
      }
		});


		const project = projectRef.current;
		// bg color
		gsap.utils.toArray(".clr").forEach((item) => {
			let color = item.getAttribute("data-bgcolor");

			ScrollTrigger.create({
					trigger: item,
					start: "top 50%",
					end: "bottom 5%",
					// markers: true,

					onEnter: () => gsap.to(project, {
							backgroundColor: color,
							duration: 1,
					}),
					onEnterBack: () => gsap.to(project, {
							backgroundColor: color,
							duration: 1,
					}),
			});
		});

    let tl = gsap.timeline({
      scrollTrigger: {
        trigger: textRef.current,
        start: "top bottom",
        toggleActions: "play none none none",
      }
    });

    tl.from(textRef.current, {
      y: 100,
      opacity: 0,
      duration: 1,
      ease: "power1.out",
    });


	}, []);

  return (
		<Layout header footer>
			<div id="project" ref={projectRef} className='project-wrap lsgpis'>

				<section ref={topRef} className='project-top'>
					<h2>LS GPIS</h2>
					<div className='project-topimg'>
						<img src={Topimg} alt=" "/>
					</div>
				</section>

				<section className="area-one clr" data-bgcolor="#ffffff">
					<div className="area-half">
						<div className="left">
							<strong className="title">LS GPIS  </strong>
							<div className="dl-info">
								<dl>
									<dt>Category.</dt>
									<dd>Web Platform, Admin</dd>
								</dl>
								<dl>
									<dt>Type.</dt>
									<dd>PC</dd>
								</dl>
								<dl>
									<dt>Date.</dt>
									<dd>2022.07.01 ~ 2022.07.31</dd>
								</dl>
							</div>
						</div>
						<div className="right">
							<div className="description">
								<strong>Description.</strong>
								<p>
									        .<br/>
									Figma    . HTML5 CSS3      javascript JQuery  lnb      .
									, chart.js          . 
								</p>
							</div>
							<div className="description">
								<strong>Skills.</strong>
								<p>HTML, CSS, Javascript, jQuery, ASP, PhotoShop, Illustrator</p>
							</div>
						</div>
					</div>
					<a href="https://pmineq.github.io/admin/LSGPIS/main.html" className="btn-goto" target="_blank" rel="noreferrer"> </a>
				</section>

				<hr/>

				<section className="area-one">
					<img src={Lsgpis01} alt="LSGPIS "/>
				</section>

				<section className="area-one clr" data-bgcolor="#ffffff">
					<strong className="sub-title">,<br/>UI/UX ,<br/>Web Publishing .</strong>
					<div className="area-half">
						<div className="left description">
							<img src={Lsgpis02} alt="LSGPIS "/>
							<div className="discription">
								<strong> .</strong>
							</div>
						</div>
						<div ref={textRef} className="right">
							<div className="dl-info">
								<dl>
									<dt>, .</dt>
									<dd>
										<ul className="list">
											<li>  </li>
											<li>Chart.js      .</li>
										</ul>
									</dd>
								</dl>
								<dl>
									<dt> .</dt>
									<dd>    .</dd>
								</dl>
								<dl>
									<dt> .</dt>
									<dd>
										             .
									</dd>
								</dl>
							</div>
						</div>
					</div>
				</section>

				<section className="area-one clr" data-bgcolor="#ffffff">
					<strong className="sub-title">  ,<br/>      .</strong>
					<div className="area-half">
						<div className="left">
							<img src={Lsgpis04} alt="LSGPIS "/>
							<div className="description">
								<strong>     +  </strong>
							</div>
						</div>
						<div className="right">
							<img src={Lsgpis05} alt="LSGPIS "/>
							<div className="description">
								<strong> </strong>
							</div>
							<img src={Lsgpis06} alt="LSGPIS "/>
							<div className="description">
								<strong> </strong>
							</div>
						</div>
					</div>
				</section>

				<section className="area-one ls-bghalf clr" data-bgcolor="#ffffff">
					<img src={Lsgpis03} alt="LSGPIS "/>
				</section>
			</div>
			
		</Layout>
	);
};



export default Lsgpis;