import React, { useEffect, useRef } from 'react';
import { Link } from 'react-router-dom';

import '../assets/scss/intro.scss';

import Layout from '../components/Layout';

import gsap from 'gsap';




const Intro = () => {

	const titleRef = useRef();
	const emRef = useRef();

	useEffect(() => {
		let tl = gsap.timeline(); //
		tl.from(titleRef.current, {
			y: 100,
			opacity: 0,
			duration: 1,
			ease: "ease.out",
		});

		tl.from(emRef.current, {
			backgroundSize: 0,
			opacity: 0,
			duration: 1.5,
			ease: "sine.out",
		});

	}, []);


	

  return (
		<Layout header>
			<div className='intro-wrap'>
				<div ref={titleRef} className='intro-text'>
					<h2>  <br/>4<span className='plus'>+</span> UI  .</h2>
					<p>
						<strong> </strong> <strong>UI/UX </strong>, <strong></strong>   , <br/>
						<em ref={emRef}>---&gt;</em>  <strong>  </strong>.
					</p>
					<div className='skills'>
						<strong>Skills.</strong>
						<p>HTML5, CSS3, SCSS, Javascript, jQuery, React, Three.js, Figma, PhotoShop, Illustrator, Github</p>
					</div>
					<div className='intro-btn-group'>
						<Link to='/main' className='btn'></Link>
						<Link to='/project' className='btn'> </Link>
					</div>
				</div>
			</div>
		</Layout>
	);
};


export default Intro;