import React, { useEffect, useRef } from "react";
import $ from 'jquery';
import gsap from 'gsap';
import { ScrollTrigger } from "gsap/ScrollTrigger";

import Layout from '../../components/Layout';
import '../../assets/scss/portfolio.scss';
import Topimg from '../../assets/images/project/hyundai.png';
import Hyundai01 from '../../assets/images/project/hyundai_sub01.jpg';
import Hyundai02 from '../../assets/images/project/hyundai_sub02.jpg';
import Hyundai03 from '../../assets/images/project/hyundai_sub03.png';
import Hyundai04 from '../../assets/images/project/hyundai_sub04.jpg';


const Hyundai = () => {

	gsap.registerPlugin(ScrollTrigger);
	const projectRef = useRef();
	const topRef = useRef();

	const textRef = useRef();

	useEffect(() => {

		$('#header').addClass('white difference');

		gsap.to(topRef.current,{
			height: '70vh',
			duration: 3,
			onComplete: () => {
        //    ScrollTrigger 
        ScrollTrigger.refresh();
      }
		});


		const project = projectRef.current;
		// bg color
		gsap.utils.toArray(".clr").forEach((item) => {
			let color = item.getAttribute("data-bgcolor");

			ScrollTrigger.create({
					trigger: item,
					start: "top 50%",
					end: "bottom 5%",
					// markers: true,

					onEnter: () => gsap.to(project, {
							backgroundColor: color,
							duration: 1,
					}),
					onEnterBack: () => gsap.to(project, {
							backgroundColor: color,
							duration: 1,
					}),
			});
		});

    let tl = gsap.timeline({
      scrollTrigger: {
        trigger: textRef.current,
        start: "top bottom",
        toggleActions: "play none none none",
      }
    });

    tl.from(textRef.current, {
      y: 100,
      opacity: 0,
      duration: 1,
      ease: "power1.out",
    });


	}, []);

  return (
		<Layout header footer>
			<div id="project" ref={projectRef} className='project-wrap hyundai'>

				<section ref={topRef} className='project-top'>
					<h2>Shop</h2>
					<div className='project-topimg'>
						<img src={Topimg} alt=" "/>
					</div>
				</section>

				<section className="area-one clr" data-bgcolor="#ffffff">
					<div className="area-half">
						<div className="left">
							<strong className="title">   </strong>
							<div className="dl-info">
								<dl>
									<dt>Category.</dt>
									<dd>Web/Mobile Platform, E-commerce</dd>
								</dl>
								<dl>
									<dt>Type.</dt>
									<dd></dd>
								</dl>
								<dl>
									<dt>Date.</dt>
									<dd>2021.12 ~ 2022.03,  </dd>
								</dl>
							</div>
						</div>
						<div className="right">
							<div className="description">
								<strong>Description.</strong>
								<p>
									         .
									  ,       .
									gnb    scroll percent,           Javascript   .
									            .
								</p>
							</div>
							<div className="description">
								<strong>Skills.</strong>
								<p>HTML, CSS, Javascript, jQuery, ASP, PPT</p>
							</div>
						</div>
					</div>
					<a href="https://shop.hyundai.com/" className="btn-goto" target="_blank" rel="noreferrer"> </a>
				</section>

				<hr/>

				<section className="area-one">
					<img src={Hyundai01} alt="Shop "/>
				</section>

				<section className="area-one">
					<strong className="sub-title">, , <br/>Web Publishing .</strong>
					<div className="area-half">
						<div className="left description">
							<img src={Hyundai02} alt="Shop "/>
							<img src={Hyundai04} alt="Shop "/>
						</div>
						<div ref={textRef} className="right">
							<div className="dl-info">
								<dl>
									<dt>.</dt>
									<dd>
										<ul className="list">
											<li>  </li>
											<li>header, gnb, contents, footer        . (   class, id  rd_ )</li>
											<li>PC     gnb Swiper.js     .</li>
											<li>header-bar      width  javascript  .</li>
										</ul>
									</dd>
								</dl>
								<dl>
									<dt>.</dt>
									<dd>
										 - ,   (  list    hashtag )<br/>
										 - , ,  ...<br/>
										 - , /,   , , , 1:1, ,  ...<br/>
									</dd>
								</dl>
								<dl>
									<dt>  01.</dt>
									<dd> - tab, list    .</dd>
								</dl>
								<dl>
									<dt>  02.</dt>
									<dd>
										 - slick.js    ,      .<br/>
										(768px)         .
									</dd>
								</dl>
								<dl>
									<dt>  03.</dt>
									<dd>
										 -    .<br/>
										PC      css   slick.js  pc      .
									</dd>
								</dl>
								<dl>
									<dt>  01.</dt>
									<dd>() -       UI ,          popup  .</dd>
								</dl>
								<dl>
									<dt>  02.</dt>
									<dd> () -   Winwheel.js     UI  .</dd>
								</dl>
							</div>
						</div>
					</div>
				</section>


				<section className="area-one hd-bghalf">
					<img src={Hyundai03} alt="Shop "/>
				</section>
			</div>

		</Layout>
	);
};



export default Hyundai;