import React, { useEffect, useRef } from "react";
import $ from 'jquery';
import gsap from 'gsap';
import { ScrollTrigger } from "gsap/ScrollTrigger";

import Layout from '../../components/Layout';
import '../../assets/scss/portfolio.scss';
import Topimg from '../../assets/images/project/admin.png';
import Admin01 from '../../assets/images/project/admin_sub01.jpg';
import Admin02 from '../../assets/images/project/admin_sub02.jpg';


const Admin = () => {

	gsap.registerPlugin(ScrollTrigger);
	const projectRef = useRef();
	const topRef = useRef();

	const textRef = useRef();

	useEffect(() => {

		$('#header').addClass('white difference');

		gsap.to(topRef.current,{
			height: '70vh',
			duration: 3,
			onComplete: () => {
        //    ScrollTrigger 
        ScrollTrigger.refresh();
      }
		});


		const project = projectRef.current;
		// bg color
		gsap.utils.toArray(".clr").forEach((item) => {
			let color = item.getAttribute("data-bgcolor");

			ScrollTrigger.create({
					trigger: item,
					start: "top 50%",
					end: "bottom 5%",
					// markers: true,

					onEnter: () => gsap.to(project, {
							backgroundColor: color,
							duration: 1,
					}),
					onEnterBack: () => gsap.to(project, {
							backgroundColor: color,
							duration: 1,
					}),
			});
		});

    let tl = gsap.timeline({
      scrollTrigger: {
        trigger: textRef.current,
        start: "top bottom",
        toggleActions: "play none none none",
      }
    });

    tl.from(textRef.current, {
      y: 100,
      opacity: 0,
      duration: 1,
      ease: "power1.out",
    });


	}, []);

  return (
		<Layout header footer>
			<div id="project" ref={projectRef} className='project-wrap admin'>

				<section ref={topRef} className='project-top'>
					<h2>Admin</h2>
					<div className='project-topimg'>
						<img src={Topimg} alt=" "/>
					</div>
				</section>

				<section className="area-one clr" data-bgcolor="#ffffff">
					<div className="area-half">
						<div className="left">
							<strong className="title"> Admin  </strong>
							<div className="dl-info">
								<dl>
									<dt>Category.</dt>
									<dd>Toy Project</dd>
								</dl>
								<dl>
									<dt>Type.</dt>
									<dd></dd>
								</dl>
								<dl>
									<dt>Date.</dt>
									<dd>2022.02</dd>
								</dl>
							</div>
						</div>
						<div className="right">
							<div className="description">
								<strong>Description.</strong>
								<p>
									   .<br/>
									 ,   .<br/>
									         .
									        .
								</p>
							</div>
							<div className="description">
								<strong>Skills.</strong>
								<p>HTML, CSS, Javascript, jQuery</p>
							</div>
						</div>
					</div>
					<a href="https://pmineq.github.io/admin/GTEC/Web/components.html" className="btn-goto" target="_blank" rel="noreferrer"> </a>
				</section>

				<hr/>

				<section className="area-one">
					<img src={Admin01} alt="Admin "/>
				</section>

				<section className="area-one clr" data-bgcolor="#0061b2">
					<strong ref={textRef} className="sub-title">, ,<br/>Web Publishing .</strong>
				</section>

				<section className="area-one">
					<img src={Admin02} alt="Admin "/>
					<div className="description">
						<strong>   </strong>
					</div>
				</section>

			</div>
			
		</Layout>
	);
};



export default Admin;